import React from "react";
import { Box, fade, Grid, Typography } from "@material-ui/core";
import { colors } from "../theme/colors.theme";
import ParticlesComponent from "./Particles";

const Home = ({ desktop }) => {
  const images = [
    {
      character: "/assets/images/parallax/ryze-min.png",
      background: "/assets/images/parallax/background.jpg",
    },
    {
      character: "/assets/images/parallax/batman-min.png",
      background: "/assets/images/parallax/background.jpg",
    },
    {
      character: "/assets/images/parallax/deadpool-min.png",
      background: "/assets/images/parallax/background.jpg",
    },
    {
      character: "/assets/images/parallax/spider-min.png",
      background: "/assets/images/parallax/background.jpg",
    },
  ];

  const image = images[Math.floor(Math.random() * images.length)];

  return (
    <>
      {desktop ? (
        <>
          <ParticlesComponent zIndex={-1} />
          <Box
            width="100%"
            height="100%"
            display="flex"
            alignItems="center"
            justifyContent="center"
            zIndex={10}
          >
            <Box>
              <Typography variant={desktop ? "h2" : "h6"}>
                I'm Fernando,
              </Typography>
              <Typography variant={desktop ? "h2" : "h6"}>
                Software Engineer
              </Typography>
              <Typography
                variant="h6"
                style={{ color: fade(colors.white, 0.6) }}
              >
                Front-End Developer
              </Typography>
            </Box>
            <Box ml={5}>
              <img
                style={{
                  width: 200,
                  borderRadius: 360,
                  border: `5px solid ${colors.blueFernando}`,
                  backgroundColor: fade(colors.blueFernando, 0.5),
                }}
                src="./assets/images/me-min.png"
                alt="Fernando Briceño"
              />
            </Box>
            <Box
              height="100%"
              width="100%"
              position="absolute"
              zIndex={-1}
              bgcolor={fade(colors.black, 0.6)}
            />
            <Box
              height="100vh"
              width="100%"
              textAlign="center"
              position="absolute"
              zIndex={-2}
              style={{
                backgroundImage: `url(./assets/images/parallax/background.jpg)`,
              }}
            />
            <Box
              height="100vh"
              width="100%"
              position="absolute"
              zIndex={-2}
              className="parallax-el"
              style={{
                backgroundImage: `url(${image.character})`,
                backgroundSize: "cover",
              }}
            />
          </Box>
        </>
      ) : (
        <>
          <ParticlesComponent zIndex={1} />
          <Box
            style={{
              backgroundImage: `url(./assets/images/parallax/background.jpg)`,
            }}
            display="flex"
            alignItems="center"
            justifyContent="center"
            height="100vh"
            p={2}
          >
            <Grid container justify="center" alignItems="center">
              <Grid item xs={12} md={3}>
                <Box textAlign="center">
                  <img
                    style={{
                      width: "100%",
                      maxWidth: 150,
                      borderRadius: 360,
                      border: `5px solid ${colors.blueFernando}`,
                      backgroundColor: fade(colors.blueFernando, 0.5),
                    }}
                    src="./assets/images/me-min.png"
                    alt="Fernando Briceño"
                  />
                </Box>
              </Grid>
              <Grid item xs={12} md={9}>
                <Box textAlign="center">
                  <Typography
                    variant={desktop ? "h2" : "h5"}
                    style={{ fontWeight: 800 }}
                  >
                    I'm Fernando,
                  </Typography>
                  <Typography
                    variant={desktop ? "h2" : "h5"}
                    style={{ fontWeight: 800 }}
                  >
                    Software Engineer
                  </Typography>
                  <Typography
                    variant="h6"
                    style={{ color: fade(colors.white, 0.6) }}
                  >
                    Front-End Developer
                  </Typography>
                </Box>
              </Grid>
            </Grid>
          </Box>
        </>
      )}
    </>
  );
};

export default Home;
